import {
  trigger,
  transition,
  style,
  query,
  group,
  animate,
} from '@angular/animations';

export const Slider =
  trigger('routeAnimations', [
      transition('* => index', [
        query(':enter, :leave', [
          style({
            position: 'relative',
            left: 0,
          })
        ], { optional: true }),
        group( [
          query(':enter', [
            animate('300ms ease', style({ left: '100%'}))
        ])]),
        group([
          query(':enter', [
            style({ left: '-100%'})
          ], { optional: true }),
          query(':enter', [
            animate('300ms ease', style({ left: '0%'}))
          ])
        ]),
      ]),
      transition('index => *', [
        query(':enter, :leave', [
          style({
            position: 'relative',
            right: 0,
          })
        ], { optional: true }),
        group( [
          query(':enter', [
            animate('300ms ease', style({ right: '100%'}))
          ])]),
        group([
          query(':enter', [
           style({ right: '-100%'})
          ], { optional: true }),
          query(':enter', [
            animate('300ms ease', style({ right: '0%'}))
          ])
        ]),
      ]),
      transition('* => resume', [
        query(':enter, :leave', [
          style({
            position: 'relative',
            right: 0,
          })
        ], { optional: true }),
        group( [
          query(':enter', [
            animate('300ms ease', style({ right: '100%'}))
          ])]),
        group([
          query(':enter', [
           style({ right: '-100%'})
          ], { optional: true }),
          query(':enter', [
            animate('300ms ease', style({ right: '0%'}))
          ])
        ]),
      ]),
      transition('resume => *', [
        query(':enter, :leave', [
          style({
            position: 'relative',
            left: 0,
          })
        ], { optional: true }),
        group( [
          query(':enter', [
            animate('300ms ease', style({ left: '100%'})),
          ])]),
        group([
          query(':enter', [
            style({ left: '-100%'})
          ], { optional: true }),
          query(':enter', [
            animate('300ms ease', style({ left: '0%'}))
          ])
        ]),
      ])
    ],
  );
